import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DashboardComponent } from './dashboard/dashboard.component';
import { ProgressComponent } from './progress/progress.component';
import { Grafica1Component } from './grafica1/grafica1.component';
import { AccountSettingsComponent } from './account-settings/account-settings.component';
import { PromesasComponent } from './promesas/promesas.component';
import { RxjsComponent } from './rxjs/rxjs.component';
import { PerfilComponent } from './perfil/perfil.component';

// Mantenimientos
import { UsuariosComponent } from './mantenimientos/usuarios/usuarios.component';
import { HospitalesComponent } from './mantenimientos/hospitales/hospitales.component';
import { MedicosComponent } from './mantenimientos/medicos/medicos.component';
import { MedicoComponent } from './mantenimientos/medicos/medico.component';
import { BusquedaComponent } from './busqueda/busqueda.component';
import { AdminGuard } from '../guards/admin.guard';
import { FotosComponent } from './diseño/fotos/fotos.component';
import { FotoComponent } from './diseño/fotos/foto.component';
import { BlogComponent } from './diseño/blog/blog.component';
import { BlogsComponent } from './diseño/blog/blogs.component';
import { NoticiasComponent } from './diseño/noticias/noticias.component';
import { NoticiaComponent } from './diseño/noticias/noticia.component';
import { ApiComponent } from './helpme/api.component';
import { InformationComponent } from './helpme/information.component';
import { CertificationComponent } from './helpme/certification.component';
import { CronogrmaComponent } from './diseño/cronogrma/cronogrma.component';
import { DeveloperComponent } from './helpme/developer.component';
import { MyPerfilComponent } from './perfil/myperfil.component';
import { InstitucionComponent } from './mantenimientos/institucion/institucion.component';
import { QuestionsComponent } from './diseño/questions/questions.component';
import { QuestionComponent } from './diseño/questions/question.component';




const childRoutes: Routes = [
  { path: '', component: DashboardComponent, data: { titulo: 'Dashboard' } },
  { path: 'account-settings', component: AccountSettingsComponent, data: { titulo: 'Ajustes de cuenta' }},
  { path: 'buscar/:termino', component: BusquedaComponent, data: { titulo: 'Busquedas' }},
  { path: 'grafica1', component: Grafica1Component, data: { titulo: 'Gráfica #1' }},
  { path: 'perfil', component: PerfilComponent, data: { titulo: 'Actualizar usuario' }},
  { path: 'myperfil', component: MyPerfilComponent, data: { titulo: 'Perfil de usuario' }},
  { path: 'progress', component: ProgressComponent, data: { titulo: 'ProgressBar' }},
  { path: 'promesas', component: PromesasComponent, data: { titulo: 'Promesas' }},
  { path: 'rxjs', component: RxjsComponent, data: { titulo: 'RxJs' }},

  // Mantenimientos
  { path: 'hospitales', component: HospitalesComponent, data: { titulo: 'Categorias' }},
  { path: 'medicos', component: MedicosComponent, data: { titulo: 'Personal Institucional' }},
  { path: 'school', component: InstitucionComponent, data: { titulo: 'Configuración de Institución' }},
  { path: 'medico/:id', component: MedicoComponent, data: { titulo: 'Usuario' }},
  
  // Diseño
  { path: 'photos', component: FotosComponent, data: { titulo: 'Fotos' }},
  { path: 'photo/:id', component: FotoComponent, data: { titulo: 'Fotos' }},
  { path: 'blog', component: BlogComponent, data: { titulo: 'blogs' }},
  { path: 'blogs/:id', component: BlogsComponent, data: { titulo: 'blog' }},
  { path: 'news', component: NoticiasComponent, data: { titulo: 'Noticias' }},
  { path: 'news/:id', component: NoticiaComponent, data: { titulo: 'Noticia' }},
  { path: 'questions', component: QuestionsComponent, data: { titulo: 'Preguntas Frecuentes' }},
  { path: 'question/:id', component: QuestionComponent, data: { titulo: 'Pregunta' }},
  { path: 'cronograma', component: CronogrmaComponent, data: { titulo: 'Cronograma' }},
  
  //HELPME
  { path: 'api', component: ApiComponent, data: { titulo: 'API REST' }},
  { path: 'information', component: InformationComponent, data: { titulo: 'Información' }},
  { path: 'certification', component: CertificationComponent, data: { titulo: 'Certificados' }},
  { path: 'developer', component: DeveloperComponent, data: { titulo: 'Desarrolladores' }},
   
  
  // Rutas de Admin
  { path: 'usuarios', canActivate: [ AdminGuard ], component: UsuariosComponent, data: { titulo: 'Matenimiento de Usuarios' }},
]



@NgModule({
  imports: [ RouterModule.forChild(childRoutes) ],
  exports: [ RouterModule ]
})
export class ChildRoutesModule { }
